.main_block_things{
	display: flex;
	width: 100%;
	flex-direction: column;
	min-height: 77vh;
	align-items: center;
}

.block_things__title{
	font-size: 48px;
	margin-top: 20px;
	margin-bottom: 20px; 
}

.things_cards{
	display: flex;
	flex-direction: column;
	width: 100%;
	justify-content: space-between;
}

.things_card{
	width: 50%;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	border: 1px solid rgba(0, 0, 0, 0.5);
	box-sizing: border-box;
	border-radius: 15px;
	padding: 15px;
	margin-bottom: 16px;
}

.things_card > .card_info, .things_card > .card_stat{
	display: flex;
	align-items: center;
}

.things_card > .card_info > .card__numb{
	font-size: 96px;
}

.things_card > .card_info > .card__img{
	margin-right: 5px;
}