.block__lk{
	display: flex;
	min-height: 77vh;
}

.block_lk__list{
	width: 40%;
	display: flex;
	min-height: 77vh;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	font-size: 24px;
	list-style-type: none;
	background-color: #EBF0F3;
}

.block_lk__list > .lk__item{
	display: flex;
	height: 80px;
	width: 100%;
	cursor: pointer;
	padding-left: 35px;
	line-height: 80px;
}

.lk__item.react-tabs__tab--selected{
	background-color: white;
	font-weight: bold;
}

.block_lk__info{
	display: none;
	padding: 30px;
	overflow-y: auto;
	width: 50%;
	background-color: white;
	border-radius: 10px;
	box-shadow: -5px 4px 10px rgba(140, 140, 140, 0.25);
}

.block_lk__info.react-tabs__tab-panel--selected{
	display: block;
}

.lk_profile__title{
	font-weight: normal;
	font-size: 48px;
}

.user_avatar{
	display: block;
	margin-bottom: 30px;
}

.row{
	display: flex;
	/*flex-wrap: wrap;*/
	-webkit-flex-direction: row;
	-moz-flex-direction: row;
	-ms-flex-direction: row;
	-o-flex-direction: row;
	flex-direction: row;
}

#load_ava{
	visibility: hidden;
}

.col-4{
	width: 100%;
/*	flex: 0 0 33.333333%;
    max-width: 33.333333%;*/
}

.col-8 {
/*    -ms-flex: 0 0 66.666667%;
    flex: 0 0 66.666667%;
    max-width: 66.666667%;*/
    width: 100%;
}

.flex_center{
	display: flex;
	flex-direction: column;
	align-items: center;
}

.user__param {
	margin-bottom: 20px;
}

.profile__btn {
	width: 250px;
	height: 40px;
	text-align: center;
	font-weight: bold;
	font-size: 24px;
	line-height: 29px;
	color: #fff;
	background: linear-gradient(249.21deg, #2157F4 13.35%, #5960EA 87.3%);
	box-shadow: 5px 5px 10px rgba(121, 121, 121, 0.5);
	border-radius: 15px;
}

.form__btn {
	width: 65%;
	height: 40px;
	margin: 0 17.5%;
	border-radius: 15px;
	text-align: center;
	color: #fff;
	font-weight: bold;
	font-size: 24px;
	line-height: 29px;
	background: linear-gradient(249.21deg, #2157F4 13.35%, #5960EA 87.3%);
	box-shadow: 5px 5px 10px rgba(121, 121, 121, 0.5);
	border-radius: 15px;
}

/*Конфиденциальность*/

.same__title {
	font-size: 48px;
	line-height: 58px;
	text-transform: uppercase;
	margin-bottom: 10px;
}

.same__text {
	width: 90%;
	font-size: 24px;
	line-height: 29px;
}

.confid__form {
	width: 600px;
	margin: 57px auto;
}

.confid__form input {
	width: 500px;
	height: 50px;
}