.main_block{
	display: flex;
	height: 77vh;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	background: linear-gradient(249.05deg, #5960EA 11.28%, #0057B6 88.59%);
	color: white;
}

p {
    margin-block-start: 0;
    margin-block-end: 0;
}

h3 {
    margin-block-start: 0;
    margin-block-end: 0;
}

.title{
	font-size: 48px;
	width: 55%;
	text-align: left;
	margin-left: 10%;
	margin-bottom: 27px;
}

.subtitle{
	font-size: 24px;
	margin-left: 10%;
	margin-bottom: 27px;
}

.main__link{
	margin-left: 10%;
}

.main__btn {
	width: 300px;
	height: 50px;
	box-shadow: 5px 5px 10px rgba(0, 62, 129, 0.75);
	border-radius: 15px;
}

.footer {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-webkit-flex-direction: row;
	-moz-flex-direction: row;
	-ms-flex-direction: row;
	-o-flex-direction: row;
	flex-direction: row;
	justify-content: space-around;
	-ms-align-items: center;
	align-items: center;
	width: 100%;
	background-color: #fff;
	height: 70px;
}


.footer__icons {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-webkit-flex-direction: row;
	-moz-flex-direction: row;
	-ms-flex-direction: row;
	-o-flex-direction: row;
	flex-direction: row;
	-ms-align-items: center;
	align-items: center;
	justify-content: center;
}

.footer__icons img {
	margin-right: 20px;
}

.footer__text {
	font-size: 24px;
	line-height: 29px;
	color: #262C40;
}