.main_block_reg{
	display: flex;
	flex-direction: column;
	min-height: 77vh;
	justify-content: center;
}

.main_block_reg > .block_reg__title{
	font-weight: bold;
	font-size: 24px;
	margin-bottom: 20px;
}

.main_block_reg > .link_more{
	font-weight: 300;
	font-size: 15px;
	font-family: Roboto;

	margin-top: 10px;
}

.input_reg{
	display: block;
	width: 28%;
	margin: 0 auto 10px;
}

.select_reg{
	display: block;
	width: 28%;
	margin: 0 auto 10px;
}

.select_reg_height{
	height: 56px;
}

.text-center{
	text-align: center;
}

.button_reg{
	display: block;
	width: 28%;
	margin: 0 auto;	
}

.button_reg > span{
	width: auto;
}